import Vue from 'vue'
import Vuex from 'vuex'
import router from './router.js'

Vue.use(Vuex);

let store = new Vuex.Store({
    state: {
        // baseurl:'http://manager.pvwyeguan.fminxiang.com',
        // baseurl:'http://manager.yeguan.fminxiang.com', //
        baseurl:'http://test.manager.yeguan.fminxiang.com', //
		// baseurl:'http://manager.yeguan.com',
        loading: false,
        isFetchData: true,
        pageTitle:''
    },
    getters: {

    },
    mutations: {
        showLoading(state) {
            state.loading = true;
        },
        hideLoading(state) {
            state.loading = false;
        },
        toLogin(state, msg){
            localStorage.removeItem('info');
            localStorage.removeItem('token');
            layer.open({
                content: '',
                btn: '',
                shadeClose: false,
                yes: function (index) {
                    layer.close(index);
                    router.push({
                        path: '/login'
                    })
                }
            });
        },
        msg(state, resmsg) {
            layer.open({
                content: resmsg
                , skin: 'msg'
                , time: 2
            });
        }
    },
    actions: {

    }
});

export default store


