<template>
	<div class="adr_input_show">
		<div class="row flex_align require">
			<div class="left flex_1">()</div>
			<div class="right flex_1 text_r"><input type="text" v-model="formData.real_name" placeholder=""></div>
		</div>
		<div class="row flex_align require">
			<div class="left flex_1">()</div>
			<div class="right flex_1 text_r arrow">
                <component :is="'PickSelect'" :pickSelectData="customTeam" @pickSelectVal="customTeamVal">{{customTeam.def.text}}</component>
            </div>
		</div>
		<div class="row flex_align" v-show="customTeam.def.id == 1? true : false">
			<div class="left flex_1"></div>
			<div class="right flex_1 text_r arrow"><component :is="'PickSelect'" :pickSelectData="customState" @pickSelectVal="customStateVal">{{customState.def.text}}</component></div>
		</div>
		<div class="row flex_align require">
			<div class="left flex_1">()</div>
			<div class="right flex_1 text_r"><input type="tel" v-model="formData.mobile" placeholder=""></div>
		</div>
        <div class="card_face">
            <div class="title flex_align">
                <div class="flex_1"></div>
                <div class="not_mainland" @click="isMainland"><span><b v-show="is_mainland"><img src="/public/img/check_yes.png" alt=""></b></span></div>
            </div>
            <div class="cardbox cr">
                <div class="example">
                    <img src="/public/img/card_face_example.jpg" alt="">
                </div>
                <div class="card">
                    <component :is="'UploadPic'" ref="touxiangmianRef" :uploadProp="formData.idcard_img" @uploadEmit="touxiangmianFn" class="upload_pic" id="touxiangmian"></component>
                </div>
            </div>
        </div>
        <div class="shibie" v-show="shibieTip">{{shibieTip}}</div>
        <div class="row flex_align">
            <div class="left"></div>
            <div class="right flex_1 text_r">
                <input type="tel" class="idcard_ipt" v-model="formData.idcard" placeholder="" v-show="canIptNum">
                <input type="tel" class="idcard_ipt" v-model="formData.idcard" placeholder="" v-show="!canIptNum" readonly unselectable="on" onfocus="this.blur()" >
            </div>
        </div>
        <div class="row flex_align">
			<div class="left"></div>
			<div class="right flex_1 text_r">
                <input type="text" class="idcard_address_ipt" v-model="formData.idcard_address" placeholder="" v-show="canIptNum">
                <input type="text" class="idcard_address_ipt" v-model="formData.idcard_address" placeholder="" v-show="!canIptNum" readonly unselectable="on" onfocus="this.blur()"  >
            </div>
		</div>
        <div class="row flex_align">
			<div class="left"></div>
			<div class="right flex_1 text_r">
                <input type="text" class="nation_ipt" v-model="formData.nation" placeholder="" v-show="canIptNum">
                <input type="text" class="nation_ipt" v-model="formData.nation" placeholder="" v-show="!canIptNum" readonly unselectable="on" onfocus="this.blur()"  >
            </div>
		</div>
        <div class="card_face">
            <div class="title"></div>
            <div class="cardbox cr">
                <div class="example">
                    <img src="/public/img/card_back_example.jpg" alt="">
                </div>
                <div class="card">
                    <component class="upload_pic" :is="'UploadPic'" id="guohuimian" ref="guohuimianRef" :uploadProp="formData.idcardback_img" @uploadEmit="guohuimianFn"></component>
                </div>
            </div>
        </div>
        <div class="shibie" v-show="shibieDateTip">{{shibieDateTip}}</div>
        <div class="row flex_align">
            <div class="left flex_1"></div>
            <div class="right flex_1 text_r">
                <component :is="'PickTime'" :PickTimeProp="formData.idcard_expiry_date" @PickTimeEmit="pickTimeFn" v-show="canIptDate"><input type="text" class="idcard_expiry_date_ipt" id="IdcardDate" v-model="formData.idcard_expiry_date" placeholder="" readonly></component>
                <input type="text" class="idcard_expiry_date_ipt" v-model="formData.idcard_expiry_date" v-show="!canIptDate" placeholder="" readonly="readonly" unselectable="on" onfocus="this.blur()" >
            </div>
        </div>
        <div class="row flex_align">
			<div class="left"></div>
			<div class="right flex_1 text_r"><input v-model="formData.current_address" type="text" placeholder=""></div>
		</div>
        <div class="row flex_align">
			<div class="left flex_1"></div>
			<div class="right flex_1 text_r"><input type="text" v-model="formData.email" placeholder=""></div>
		</div>
        <div class="marks">
            <div class="title"></div>
            <textarea v-model="formData.remark" placeholder=""></textarea>
        </div>
	</div>
</template>
<script>
	export default{
		data(){
			return{
                cid:'',
                canSubmit:true, //
                shibieTip:'', //
                shibieDateTip:'',  //
                canIptNum:true, //
                canIptDate:true, //
                touxiangmianFailNum:2,//
                guohuimianFailNum:2,//
                is_mainland:false, //
                formData:{
                    real_name:'',
                    mobile:'',
                    email:'',
                    is_vip:'0',
                    customer_tag:'',
                    current_address:'',
                    idcard:'',
                    idcard_address:'',
                    nation:'',
                    idcard_img:'',
                    idcardback_img:'',
                    idcard_expiry_date:'',
                    remark:''
                },
                customTeam:{
                    title:'', //
                    def:{id:'1',text:''},
                    arr:[   //
                        {id:'1',text:''},
                        {id:'2',text:''}
                    ]
                },
                customState:{
                    title:'',
                    def:{id:'1',text:''},
                    arr:[
                        {id:'1',text:''},
                        {id:'2',text:''}
                    ]
                }
			}
        },
        mounted(){
            adrShowInput();     //,input
            //
            this.cid = this.$route.params.id;
            if(this.cid){
                this.fetchData();
            }else{
                this.uploadInit();
            }
        },
        methods:{
            fetchData(){
                var _this = this;
                var data = this.qs.stringify({
                    token:localStorage.token,
					cid:_this.cid
				});
                this.axios.post('/customer/edit',data).then(function(res){
                    if(res.data.code == 1){
                        this.formData = res.data.data;
                        //
                        if(this.formData.tag !== undefined && this.formData.tag !== null ){
                            this.formData.customer_tag = this.formData.tag;
                            delete this.formData.tag;
                        }
                        
                        //
                        if(this.formData.is_vip == 1){
                            this.customTeam.def = this.customTeam.arr[1];
                        }else if(this.formData.is_vip == 0){
                            this.customTeam.def = this.customTeam.arr[0];
                            if(this.formData.customer_tag == ''){
                                this.customState.def = this.customState.arr[1];
                            }
                        }
                        //
                        this.uploadInit();
                    }else{
                        this.$store.commit('msg', res.data.message);
                    }
                }.bind(this))
            },
            //
            uploadInit(){
                var _this = this;
                setTimeout(() => {
                    _this.$refs.touxiangmianRef.uploadInit('#touxiangmian',{type:'idcard',msg:''});
                    _this.$refs.guohuimianRef.uploadInit('#guohuimian',{type:'idcard_back',msg:''});
                }, 0);
            },
            //
            customTeamVal(res){
                this.customTeam.def = res;
                if(res.id == 1){
                    this.formData.is_vip = 0;
                }else{
                    this.formData.is_vip = 1;
                }
            },
            //
            customStateVal(res){
                this.customState.def = res;
                this.formData.customer_tag = res.text;
            },
            //
            isMainland(){
                this.is_mainland = !this.is_mainland;
                if(this.is_mainland){
                    this.touxiangmianFailNum = 0;
                    this.guohuimianFailNum = 0;
                    $('.idcard_ipt').attr('placeholder', '');
                    $('.idcard_address_ipt').attr('placeholder', '');
                    $('.nation_ipt').attr('placeholder', '');
                    $('.idcard_expiry_date_ipt').attr('placeholder', '');
                }else{
                    this.touxiangmianFailNum = 2;
                    this.guohuimianFailNum = 2;
                    $('.idcard_ipt').attr('placeholder', '');
                    $('.idcard_address_ipt').attr('placeholder', '');
                    $('.nation_ipt').attr('placeholder', '');
                    $('.idcard_expiry_date_ipt').attr('placeholder', '');
                }
            },
            //
            touxiangmianFn(url){
                //
                if(this.is_mainland){
                    return false;
                }
                //
                var _this = this;
                this.formData.idcard_img = url;
                if(!url){
                    return false;
                }

                this.shibieTip = '...';
                this.canIptNum = false;    //
                let data = this.qs.stringify({
                    token:localStorage.token,
					isFront: 1,
                    image:url
				});
                //
                _this.axios.post('/customer/idcardcheck',data).then(function(res){
                    _this.canIptNum = true;    //
                    if(res.data.code == 1){
                        _this.formData.idcard = res.data.data.idcard;
                        _this.formData.idcard_address = res.data.data.idcard_address;
                        _this.formData.nation = res.data.data.native_address;
                        _this.shibieTip = '';
                    }else{
                        _this.shibieTip = '';
                        $('#IdcardNum').attr('placeholder', '');
                        $('#IdcardAddress').attr('placeholder', '');
                        //
                        shibieAgain();
                    }
                }.bind(this)).catch(function(err){
                    _this.$store.commit('msg',err);
                    $('#IdcardNum').attr('placeholder', '');
                    $('#IdcardAddress').attr('placeholder', '');
                    _this.shibieTip = '';
                    _this.canIptNum = true;
                });
                //
                function shibieAgain(){
                    if(!_this.touxiangmianFailNum){
                        return false;
                    }
                    _this.touxiangmianFailNum--;
                    _this.$refs.touxiangmianRef.shibieAgain('');
                }
            },
            //
            guohuimianFn(url){  
                //
                if(this.is_mainland){
                    return false;
                }
                //
                var _this = this;
                this.formData.idcardback_img = url;
                if(!url){
                    return false;
                }
                this.shibieDateTip = '...';
                this.canIptDate = false;
                let data = this.qs.stringify({
                    token:localStorage.token,
					isFront: 0,
                    image:url
				});
                this.axios.post('/customer/idcardcheck',data).then(function(res){
                    this.canIptDate = true;
                    if(res.data.code == 1){
                        let t = res.data.data.idcard_expiry_date + '';
                        let y = t.substr(0,4);
                        let m = t.substr(4,2);
                        let d = t.substr(6,2);
                        this.formData.idcard_expiry_date = y + '-' + m + '-' + d;
                        this.shibieDateTip = '';
                    }else{
                        this.shibieDateTip = '';
                        $('#IdcardDate').attr('placeholder', '');
                        //
                        shibieAgain();
                    }
                }.bind(this)).catch(function(err){
                    _this.$store.commit('msg',err);
                    $('#IdcardDate').attr('placeholder', '');
                    _this.shibieDateTip = '';
                    _this.canIptDate = true;    //
                });
                //
                function shibieAgain(){
                    if(!_this.guohuimianFailNum){
                        return false;
                    }
                    _this.guohuimianFailNum--;
                    _this.$refs.guohuimianRef.shibieAgain('');
                }
            },
            //
            pickTimeFn(res){
                this.formData.idcard_expiry_date = res;
            },
            //
            submit(){
                var _this = this;
                
                if(!this.canSubmit){
                    return false;
                }
                //
                var data = $.extend({}, this.formData);
                //
                if(data.real_name == ''){
                    this.$store.commit('msg','');
                    return false;
                }else if(data.mobile == ''){
                    this.$store.commit('msg','');
                    return false;
                }

                this.canSubmit = false;
                data.token = localStorage.token;
                let isVip = data.is_vip;
                data = this.qs.stringify(data);
                this.axios.post('/customer/save',data).then(function(res){
                    if(res.data.code == 1){
                        this.$store.commit('msg', res.data.message);
                        setTimeout(() => {
                            _this.$router.push({
                                path:'/custom/list',
                                query:{
                                    is_vip: isVip
                                }
                            })
                        }, 2000);
                    }else{
                        this.$store.commit('msg', res.data.message);
                        _this.canSubmit = true;
                    }
                }.bind(this));
            }
        }
	}
</script>


<style lang="less" scoped="scoped">
    .shibie{
        padding:0 15px;color:#fe6e6e;font-size:12px;padding-top:10px;
        text-align: right;
    }
	.notxt{
		color: #b5b5b5;
	}
	.row{
		font-size: 17px;
		color: #333;
		padding:0 15px;
		line-height: 1.4;
		position: relative;
		min-height: 50px;
		&:after{
			content: "";
			position: absolute;
			left: 15px;
			right: 0;
			height: 1px;
			bottom: 0;
			border-bottom: 1px solid #ddd;
			-webkit-transform-origin: 0 100%;
			transform-origin: 0 100%;
			-webkit-transform: scaleY(.5);
			transform: scaleY(.5)
		}
		.left{
            padding: 10px 0;
            padding-right: 5px;
		}
		.right{
            padding-left: 5px;
            color: #888;
            input{
                width: 100%;
                height: 50px;
                font-size: 17px;
                color: #888888;
                text-align: right;
                background-color: transparent;
                line-height: 20px;
            }
		}
        .arrow{
            padding-right: 17px;
            background: url(/public/img/arrow_icon.png) no-repeat center right;
            background-size: 10px 15px;
        }
    }
    .card_face .title{
        padding: 0 15px;
        height: 50px;
        line-height: 50px;
    }
    .cardbox{
        padding:0 15px;
        .example{
            width: 50%;
            float: left;
            padding-right: 10px;
            img{
                width: 100%;
            }
        }
        .card{
            width: 50%;
            float: right;
            padding-left: 10px;
            position: relative;
            img{
                margin:0 auto;
                width: 100%;
            }
        }
    }
    .marks{
        padding: 0 15px;
        .title{
            font-size: 17px;
            color: #333;
            line-height: 50px;
            height: 50px;
        }
        textarea{
            background-color: #f4f4f4;
            resize: none;
            width: 100%;
            padding:0;
            border-radius: 4px;
            height: 100px;
            padding:12px;
            font-size: 17px;
            color: #888888;
            line-height: 1.4;
        }
    }
    .upload_pic{
        position: relative;
    }
    .submitbox{
        padding:15px;
        .submit{
            font-size: 17px;
            color: #fff;
            background-color: #ff8b03;
            height: 50px;
            width: 100%;
            line-height: 50px;
            text-align: center;
            border-radius: 5px;
        }
    }
    .not_mainland{
        color:#666;
        span{
            vertical-align: middle;
            display: inline-block;
            width: 20px;
            height: 20px;
            border: 1px solid #ff8b03;
            border-radius: 50%;
            margin-top: -3px;
            margin-right: 5px;
            position:relative;
            b{
                display: block;
                background-color: #ff8b03;
                width: 100%;
                height: 100%;
                border-radius: 50%;
                img{
                    width:100%;
                    height:100%;
                }
            }
        }
    }
</style>



