<template>
    <div>
		<slot>
			<img class="default_pic" src="/public/img/upload_pic_default.jpg" alt="">
		</slot>
        <div id="fileList" class="uploader-list uploader-face">
			
		</div>
    </div>
</template>
<script>
    
    export default{
        data(){
            return {
				uploader:''
            }
		},
		props:['uploadProp'],
		computed:{
			imgUrl(){
				return this.uploadProp;
			}
		},
        mounted(){
			
        },
        methods:{
            uploadInit(pickel, params){
				var _this = this;
				if(cusAndroid){
					$(pickel).find('#fileList').click(function(){
						window.android.startGallery(params.type, pickel);
						window.startGallerySuccess = function(el, url) {
							layer.closeAll();
							_this.$emit('uploadEmit', url, pickel);
							_this.deletePic(el, url);
						};
					});
				}else{
					//loading
					let loading = `
						<div style="position:absolute;left:10%;top:10%;right:10%;bottom:10%;background-color:#fff;">
							<img style="margin:16px auto 0;" src="/public/img/loading.gif">
							<p style="color:#999;font-size:12px;text-align:center;margin-top:10px;">...</p>
						</div>
					`;
					//
					let failDom = `
						<div style="position:absolute;left:10%;top:10%;right:10%;bottom:10%;background-color:#fff;">
							<p style="color:#999;font-size:12px;text-align:center;margin-top:26px;line-height:1.6;padding: 0 10px;color:#f28c8c;"></p>
						</div>
					`;
					//
					let sucDom = `
						<div style="position:absolute;left:10%;top:10%;right:10%;bottom:10%;background-color:#fff;">
							<p style="color:#999;font-size:12px;text-align:center;margin-top:26px;line-height:1.6;padding: 0 10px;color:#999;"></p>
						</div>
					`;

					var vm = this;
					//,,
					var pick = pickel, accept;
					if(is_AdrIos() == 'adr'){
						accept = {
							title: 'Images',
							extensions: 'gif,jpg,jpeg,bmp,png',
							mimeTypes: 'image/*'
						}
					}else{
						accept = {
							title: 'Images',
							extensions: 'gif,jpg,jpeg,bmp,png',
							mimeTypes: 'image/jpg,image/jpeg,image/png'
						}
					};
					//
					var picklist = pickel + ' #fileList';
					//
					var server = this.axios.defaults.baseURL+'/index/upload';
					//
					_this.uploader = WebUploader.create({
						// 
						auto: false,
						// swf
						swf: 'src/plugin/uploader/dist/Uploader.swf',
						// 
						server,
						// 
						// inputflash.
						pick: pickel,
						chunked: true, //
						chunkSize: 1010000,
						fileNumLimit:1,
						formData:{
							token:localStorage.token,
							type:params.type
						},
						//
						compress:{
							compressSize: 500
						},
						// 
						accept
					});

					var uploader = _this.uploader;
					
					// 
					var $list = $(picklist);
					uploader.on( 'fileQueued', function( file ) {
						if($(pickel+ ' .uploader-del').length){
							$(pickel+ ' .uploader-del').remove();
							$(pickel+ ' .uploader-list').empty();
						}
						$list.html(loading);
						uploader.upload();
					});
					
					// itemclass, 
					uploader.on( 'uploadSuccess', function( file, res ) {
						if(res.code == 1){
							vm.$emit('uploadEmit', res.data.fullImgUrl, pickel);
							vm.deletePic(pickel,res.data.fullImgUrl);
							$list.html(sucDom);
						}else{
							$list.html(failDom);
						}
					});

					// 
					uploader.on( 'uploadComplete', function( file ) {
						uploader.removeFile( file.id, true );
					});
				}
				//
				if(this.imgUrl){
					_this.deletePic(pickel, this.imgUrl);
				}
			},
			//
			addFile(file){
				var _this = this;
				_this.uploader.addFiles(file);
			},
			//
			shibieAgain(layerContent,type,pickel){
				var _this = this;
				if(is_AdrIos() == 'adr'){
					layer.open({
						content:layerContent,
						shadeClose:false,
						btn:[''],
						yes:function(index){
							// layer.close(index);
							if(cusAndroid){
								window.android.startGallery(type, pickel);
							}else{
								var html = `<input type="file" accept="image/jpg,image/jpeg,image/png" id="selectPic" style="display:none;">`;
								$('body').append(html);
								iptChange('#selectPic');
								// $('#selectPic').triggerFastClick();
								$('#selectPic').click();
							}
						},
						no:function(){
							if(cusAndroid){
								window.android.startGallery(type, pickel);
							}else{
								var html = `<input type="file" accept="image/*" capture="camera" id="selectPic" style="display:none;">`;
								$('body').append(html);
								iptChange('#selectPic');
								$('#selectPic').click();
								// $('#selectPic').triggerFastClick();
							}
						}
					})
				}else{
					layer.open({
						content:layerContent,
						shadeClose:false,
						btn:[''],
						yes:function(index){
							var html = `<input type="file" accept="image/jpg,image/jpeg,image/png" id="selectPic" style="display:none;">`;
							$('body').append(html);
							iptChange('#selectPic', index);
							$('#selectPic').click();
							// $('#selectPic').triggerFastClick();
						}
					})
				}
				//
                function iptChange(el, index){
                    $(el).change(function(){
                        var file = $(this)[0].files[0];
                        _this.addFile(file);
                        if(is_AdrIos() != 'adr'){
                            layer.close(index);
                        }
                        $(el).remove();
                    });
                }
			},
			//
			deletePic(pickel,url){
				var vm = this;
				var sucHtml = `
					<div class="uploader-del">
						<img style="position:absolute;width:100%;height:100%;top:0;left:0;" src="${url}">
						<img class="deletePic" src="/public/img/up_pic_delete.png">
					</div>
				`;
				$(pickel).append(sucHtml);
				$(pickel +' .deletePic').click(function(){
					layer.open({
						content: ''
						,btn: ['','']
						,shadeClose:false
						,yes:function(index){
							layer.close(index);
							$(pickel+ ' .uploader-del').remove();
							$(pickel+ ' .uploader-list').empty();
							vm.$emit('uploadEmit', '', pickel);
						}
					});
				})
			}
        }
    }
</script>
<style lang="less">
	.default_pic{
		width: 100%;
	}
    .uploader-list{
		position: absolute;
		width: 100%;
		height: 100%;
		top:0;
		left:0;
		// z-index: 99;
	}
	.webuploader-element-invisible{
		position: absolute;
		z-index: 9;
		// background-color: red;
		width: 100%;
		height: 100%;
		left: 0;
		top:0;
		opacity: 0;
	}
	.uploader-del{
		position: absolute;
		z-index: 999;
		width: 100%;
		height: 100%;
		top:0;
		left:0;
		.deletePic{
			width:30px;
			position:absolute;
			right:-12px;
			top:-12px;
			z-index: 99;
		}
	}
</style>



