import Vue from 'vue'
import App from './App.vue'
import router from './parts/router.js'
require('./parts/filters.js')
import store from './parts/store.js'
require('./parts/axios.js')
require('./parts/loading/index.js')
import VueScroller from 'vue-scroller'
require('./parts/globalComponent/index.js')

Vue.use(VueScroller)

//
Vue.config.productionTip = false;

//app
window.cusAndroid = false;
try {
	window.android.isAndroid();
} catch (e) {

}

window.isAndroid = function() {
	cusAndroid = true;
};

//
window.adrShowInput = function(){
	if (is_AdrIos() == 'adr') {
		$('.adr_input_show input').on('focus', function () {
			var that = this;
			var windowBeforeH = window.innerHeight;  //
			var thisH = $(this).height();	  //input
			var startTop = $('.adr_input_box')[0].scrollTop;	//
			window.onresize = function () {
				var windowAfterH = window.innerHeight;	 //
				var scrollChange = windowAfterH / 2 - thisH;  //input
				if (windowAfterH < windowBeforeH) {		//
					that.scrollIntoView();				//input
					var top = $('.adr_input_box')[0].scrollTop;		//scrollIntoView

					if (top > thisH*2.5) {		//input
						that.scrollIntoView(false);
						var top = $('.adr_input_box')[0].scrollTop;
						$('.adr_input_box')[0].scrollTop = top + scrollChange;
					}else{
						$('.adr_input_box')[0].scrollTop = 0; 	//
					}
				} else {
					// $('.content')[0].scrollTop = startTop;  //
				}
			}
		});
	}
}

//
var vm = new Vue({
    el: '#app',
  	router,
  	store,
    render: h => h(App)
});


