<template>
    <div class="body_cunqu">
        <div class="content">
            <div class="card">
                <div class="top flex_align bor_b">
                    <div class="left c_999"></div>
                    <div class="right flex_1">(1100)</div>
                </div>
                <div class="middle bor_b">
                    <div class="title"></div>
                    <div class="row flex_align">
                        <div></div>
                        <div class="ipt flex_1"><input type="tel" placeholder=""></div>
                    </div>
                </div>
                <div class="bottom flex_align">
                    <div class="left flex_1">1000</div>
                    <div class="right c_light_red"></div>
                </div>
            </div>
            <div class="notice c_light_red"></div>
            <div class="com_btn"><div class="btn">,</div></div>
            <div class="com_btn"><div class="btn off">,</div></div>
        </div>
        <!--  -->
        <transition name="wallet_fade">
            <div class="wallet_dialog" v-show="dialog.mask">
                <div class="mask"></div>
                <div class="table">
                    <div class="td">
                        <transition name="wallet_bounce">
                            <div class="container" v-show="dialog.con">
                                <div class="listcon">
                                    <div class="title"><div class="close" @click="dislogHide"></div></div>
                                    <ul>
                                        <li class="flex_align bor_b">
                                            <div class="left"><img src="/public/img/wallet/jianshe_bank.png" alt=""></div>
                                            <div class="flex_1"></div>
                                            <div class="right on"></div>
                                        </li>
                                        <li class="flex_align bor_b">
                                            <div class="left"><img src="/public/img/wallet/jianshe_bank.png" alt=""></div>
                                            <div class="flex_1"></div>
                                            <div class="right"></div>
                                        </li>
                                    </ul>
                                    <div class="add_bank flex_align">
                                        <div class="left"><img src="/public/img/wallet/add_bank.png" alt=""></div>
                                        <div class="flex_1"></div>
                                    </div>
                                </div>
                            </div>
                        </transition>
                    </div>
                </div>
            </div>
        </transition>
    </div>
</template>
<script>
export default {
    data(){
        return{
            dialog:{
                mask:false,
                con:false,
            }
        }
    },
    mounted(){
        
    },
    methods:{
        //
        dislogShow(){
            let _this = this;
            _this.dialog.mask = true;
            setTimeout(() => {
                _this.dialog.con = true;
            }, 10);
        },
        dislogHide(){
            let _this = this;
            _this.dialog.con = false;
            setTimeout(() => {
                _this.dialog.mask = false;
            }, 10);
        }
    }
}
</script>



